
<template>

    <div>
        <el-tag type="warning"  style="margin-left: 20px;margin-bottom: 10px;width: 200px;height: 40px;text-align: center;line-height: 40px;font-size: 20px">购票须知</el-tag>
        <el-col :span="23" style=" padding-bottom: 30px;">
            <el-row>
                <el-col :span="3" class="el-col"  style="margin-left: 0;margin-top: 30px">
                    <p class="input-title">购票须知:</p>
                    <el-input
                        style="width: 750px;"
                        :disabled="disabled"
                        type="textarea"
                        :rows="15"
                        placeholder="请输入内容"
                        v-model="buyNotice">
                    </el-input>
                </el-col>
            </el-row>
        </el-col>

        <el-col :span="23" style=" padding-bottom: 30px;">
            <el-row>
                <el-col :span="3" class="el-col"  style="margin-left: 0;margin-top: 30px">
                    <p class="input-title">注意事项:</p>
                    <el-input
                        style="width: 750px;"
                        :disabled="disabled"
                        type="textarea"
                        :rows="15"
                        placeholder="请输入内容"
                        v-model="attentionNotice">
                    </el-input>
                </el-col>
            </el-row>


        </el-col>
        <el-col :span="23" style=" padding-bottom: 30px;">
            <el-row>
                <el-col :span="3" class="el-col"  style="margin-left: 0;margin-top: 30px">
                    <p class="input-title">退票须知:</p>
                    <el-input
                        style="width: 750px;"
                        :disabled="disabled"
                        type="textarea"
                        :rows="15"
                        placeholder="请输入内容"
                        v-model="refundNotice">
                    </el-input>
                </el-col>
            </el-row>


        </el-col>
        <el-col :span="23" style=" padding-bottom: 30px;">
            <el-row>
                <el-col :span="3" class="el-col"  style="margin-left: 0;margin-top: 30px">
                    <p class="input-title">发票须知:</p>
                    <el-input
                        style="width: 750px;"
                        :disabled="disabled"
                        type="textarea"
                        :rows="15"
                        placeholder="请输入内容"
                        v-model="invoiceNotice">
                    </el-input>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="1.5" class="el-col" style="float: left;margin-left: 580px">
                    <p class="input-title">&nbsp;</p>
                    <el-button type="primary"   @click = "edit">编辑</el-button>
                    <el-button type="primary"   @click = "save">保存</el-button>
                </el-col>

            </el-row>

        </el-col>




    </div>
</template>
<script>
    import ajax from "../model/sys/index"

    export default {
        data() {
            return {
                buyNotice:"",
                attentionNotice:"",
                invoiceNotice:"",
                refundNotice:"",
                disabled:true
        }
        },
        methods: {
            getBuyNotice(){
                ajax.getBuyNotice({},res=>{
                    if(res){
                        this.buyNotice = res.data.result.buyNotice
                        this.attentionNotice = res.data.result.attentionNotice
                        this.invoiceNotice = res.data.result.invoiceNotice
                        this.refundNotice = res.data.result.refundNotice
                    }
                })
            },
            edit(){
                this.disabled=false
            },
            save(){
                var params = {
                    buyNotice:this.buyNotice,
                    attentionNotice:this.attentionNotice,
                    invoiceNotice:this.invoiceNotice,
                    refundNotice:this.refundNotice,
                }

                ajax.updateBuyNotice( params,res=>{
                    if(res){
                        this.getBuyNotice()
                        this.disabled=true
                        this.$message({
                            showClose: true,
                            message: '保存成功',
                            type: 'success'
                        });
                    }
                })

            }
        },
        mounted(){

            this.getBuyNotice()
         }
    }
</script>
<style>
    .input-title{
        font-size: 14px !important;
        font-weight: 600;
    }
    .el-col{
        margin-left: 20px;
    }
    ::-webkit-scrollbar-thumb{
        background-color: gainsboro;
    }
    .cell{
        text-align: center;
    }
    #content-main{
        overflow: scroll;
    }
    .el-table{
        overflow: scroll;
    }
    .content-table{
        width: 100%;
        border-collapse:collapse;
        background-color: white;
        text-align: center;
    }
    .content-table th{
        height: 40px;
        border: 1px solid #F2F3F4;
        text-align: center;
        background-color: gainsboro;
    }
    .content-table td{
        height: 40px;
        border: 1px solid #F2F3F4;
    }
    .dothis{
        color: #3C9CFC;
        cursor:default;
        margin-left: 10px;
    }
    .page-f{
        margin-top: 20px;
        float: right;
    }
    .content-table span:hover{
        color: silver;
    }
    .el-col-23{
        width: 99% !important;
    }
</style>



